import React, { useEffect } from 'react';
import { FormHeader, Loader } from '../../../shared/components';
import { Field } from 'formik';
import { KntInputField, KntPhoneInputField } from '../../../shared/components/Fields';
import { InviteClientFormFooter } from './InviteClientFormFooter';

type Props = {
	SwitchClient?: React.ReactNode;
	isSubmitting: boolean;
	setFieldValue: any;
	values: any;
	onSuccess: () => void;
	resetForm: any;
	isEditing?: boolean;
	formValuesSetter?: (fieldValues: string[]) => void;
};

export function IndividualFormElements({
	SwitchClient = null,
	isSubmitting,
	setFieldValue,
	values,
	onSuccess,
	resetForm,
	isEditing = false,
	formValuesSetter = () => null
}: Props) {
	useEffect(() => {
		if (!isEditing) {
			const checkableFields = ['first_name', 'email', 'last_name', 'mobile'];

			const fieldValues = Object.keys(values).filter((field) => {
				const fieldHasValue = Array.isArray(values[field]) ? values[field].length : values[field];
				if (fieldHasValue && checkableFields.includes(field)) {
					return values[field];
				}
			});
			formValuesSetter(fieldValues);
		}
	}, [values]);

	return (
		<div>
			<FormHeader
				resetForm={resetForm}
				title={`${isEditing ? 'Edit' : 'Invite'} Individual Client`}
				isEditing={isEditing}
			/>
			{SwitchClient ? <div>{SwitchClient}</div> : null}
			<>
				<Field
					label="First Name"
					labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
					name="first_name"
					component={KntInputField}
					placeholder="Type your firstname.."
					required
					autoComplete="off"
				/>
				<Field
					label="Last Name"
					labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
					name="last_name"
					component={KntInputField}
					placeholder="Type your lastname.."
					required
					autoComplete="off"
				/>
				<Field
					type="email"
					label="Email"
					labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
					name="email"
					component={KntInputField}
					placeholder="Type your email.."
					required
					disabled={isEditing}
				/>
				<Field
					label="Phone"
					name="mobile"
					component={KntPhoneInputField}
					placeholder="Enter company mobile phone number"
					enableSearch
					required
					labelClasses="!text-xs !ml-6 !text-kgrey-welcome"
					onChangeHandler={setFieldValue}
				/>
				<div className={'flex items-center justify-end'}>
					{isSubmitting && <Loader tiled withOverlay />}
					<InviteClientFormFooter
						onSuccess={onSuccess}
						isSubmitting={isSubmitting}
						submitText={isEditing ? 'Edit Client' : 'Invite Client'}
					/>
				</div>
			</>
		</div>
	);
}
