import React, { createContext, useReducer, useContext, useCallback } from 'react';

type ContextType = {
	searchQuery: string;
	setSearchQuery: (query: string | null) => void;
	isStaff: string;
	setIsStaff: (type: string) => void;
};

// eslint-disable-next-line @typescript-eslint/no-non-null-assertion
export const ClientsManagementContext = createContext<ContextType>(null!);
type setSearchQuery = {
	type: 'SET_SEARCH_QUERY';
	payload: string | null;
};

type setIsStaff = {
	type: 'SET_IS_STAFF';
	payload: string;
};

type StateType = {
	searchQuery: string | null;
	isStaff: string;
};

type Actions = setSearchQuery | setIsStaff;

const reducer = (state: StateType, action: Actions): StateType => {
	switch (action.type) {
		case 'SET_SEARCH_QUERY': {
			return {
				...state,
				searchQuery: action.payload
			};
		}
		case 'SET_IS_STAFF': {
			return {
				...state,
				isStaff: action.payload
			};
		}
		default:
			return state;
	}
};

export function ClientsManagementContextProvider({
	children
}: React.PropsWithChildren<Record<string, unknown>>) {
	const [state, dispatch] = useReducer(reducer, { searchQuery: null, isStaff: 'true' });

	const setSearchQuery = useCallback((query: string | null) => {
		dispatch({ type: 'SET_SEARCH_QUERY', payload: query });
	}, []);
	const setIsStaff = useCallback((type: string) => {
		dispatch({ type: 'SET_IS_STAFF', payload: type });
	}, []);

	// eslint-disable-next-line react/jsx-no-constructed-context-values
	const contextValues = {
		searchQuery: state.searchQuery,
		isStaff: state.isStaff,
		setSearchQuery,
		setIsStaff
	} as ContextType;

	return (
		<ClientsManagementContext.Provider value={contextValues}>
			{children}
		</ClientsManagementContext.Provider>
	);
}

export const useClientsManagementContext = () => {
	const clientsManagementContext = useContext(ClientsManagementContext);

	if (!clientsManagementContext)
		throw new Error(
			'useClientsManagementContext must be used within the ClientsManagementContextProvider'
		);
	return clientsManagementContext;
};
