import { ClientCompanyIcon, ClientCompanyOwner } from '../../../shared/typings/client.types';
import { getClientAvatarUrl, getClientName } from '../utils';
import { Avatar } from '../../../shared/components/Elements';
import React from 'react';
import clsx from 'clsx';

type ClientNameProps = {
	is_individual: boolean;
	icon: ClientCompanyIcon;
	name: string;
	owner: ClientCompanyOwner;
	nameClassNames?: string;
};

export function ClientName({
	is_individual,
	icon,
	name,
	owner,
	nameClassNames = ''
}: ClientNameProps) {
	const clientName = getClientName(is_individual, name, owner);
	return (
		<div className="flex items-center">
			<Avatar
				src={getClientAvatarUrl(is_individual, icon, owner)}
				letter={clientName[0]}
				alt={clientName}
				className={'bg-primary-200'}
			/>
			<span className={clsx('ml-2 overflow-hidden text-ellipsis', nameClassNames)}>
				{clientName}
			</span>
		</div>
	);
}
