import { useAppState } from 'shared/state';
import { useDispatch } from 'react-redux';
import { useEntityActionPopup, useNotistack, usePageChangeAfterEntityDelete } from 'shared/hooks';
import { ClientGroupEntity } from 'shared/typings/client.types';
import { formRequest } from '../../../modules/entity/actions';

export function useClientGroups() {
	const { companyId } = useAppState();
	const dispatch = useDispatch();
	const { showNotification } = useNotistack();
	const {
		visible: deleteVisible,
		showVisible: showDeleteVisible,
		loading: deleteLoading,
		toggleLoading: toggleDeleteLoading,
		closePopup: closeDeletePopup
	} = useEntityActionPopup<ClientGroupEntity>();

	const {
		visible: editVisible,
		showVisible: showEditVisible,
		closePopup: closeEditPopup
	} = useEntityActionPopup<ClientGroupEntity>();

	const { forwardToPreviousPage } = usePageChangeAfterEntityDelete({
		pageKey: 'page',
		entity: 'clientsGroups',
		entityName: 'AllClientsGroups'
	});

	const handleClientGroupDelete = (clientGroup: ClientGroupEntity) => {
		//show loader
		toggleDeleteLoading();
		dispatch(
			formRequest({
				entity: 'clientsGroups',
				name: `AllClientsGroups`,
				method: 'delete',
				url: `/${companyId}/categories/${clientGroup.id}/`,
				deleteData: true,
				id: clientGroup.id,
				cb: {
					success: () => {
						showNotification({ message: 'Successfully deleted', variant: 'success' });
						forwardToPreviousPage();
					},
					error: (err) => {
						showNotification({ message: 'Something went wrong', variant: 'error' });
					},
					finally: () => {
						toggleDeleteLoading();
						closeDeletePopup();
					}
				},
				params: {},
				primaryKey: 'id',
				values: {}
			})
		);
	};

	const deleteClientGroupHandler = () => () => handleClientGroupDelete(deleteVisible!);

	return {
		deleteClientGroupHandler,
		deleteVisible,
		showDeleteVisible,
		deleteLoading,
		closeDeletePopup,
		showEditVisible,
		closeEditPopup,
		editVisible
	};
}
