import React from 'react';
import { useAppState } from 'shared/state';
import { useNotistack } from 'shared/hooks';
import EntityForm from 'modules/entity/forms';
import { FormHeader, Loader } from 'shared/components';
import LinkedRoleFormElements from './LinkedRoleFormElements';

type Props = {
	onSuccess: () => void;
};

export function CreateLinkedClientRole({ onSuccess }: Props) {
	const { companyId } = useAppState();
	const { showNotification } = useNotistack();

	return (
		<div>
			<>
				<EntityForm.Main
					entity="linkedClientsRole"
					name="AllLinkedClientsRole"
					url={`/${companyId}/my/company/client/roles/`}
					method="post"
					primaryKey="id"
					sendAsFormData={false}
					normalizeData={(data: any) => data}
					prependData
					fields={[
						{
							name: 'name',
							onSubmitKey: '',
							required: true
						},
						{
							name: 'description',
							onSubmitKey: '',
							required: false
						}
					]}
					onSuccess={(response, resetForm): any => {
						showNotification({ message: 'Successfully created..' });
						onSuccess();
						resetForm();
					}}
					onError={(error, resetForm): any => {
						const errorText = error?.response?.data?.message || 'Something went wrong';
						showNotification({ message: errorText, variant: 'error' });
					}}
				>
					{({ isSubmitting, values, resetForm }) => {
						if (process.env.NODE_ENV === 'development') {
							console.log('create client group  values', values);
						}
						return (
							<>
								<FormHeader resetForm={resetForm} title="Create Linked Client Role" />
								<>
									<div className={'flex items-center'}>
										<LinkedRoleFormElements isSubmitting={isSubmitting} onSuccess={onSuccess} />
										{isSubmitting && <Loader tiled withOverlay />}
									</div>
								</>
							</>
						);
					}}
				</EntityForm.Main>
			</>
		</div>
	);
}
