import { ClientCompany } from '../../../shared/typings/client.types';
import {
	useEntityActionPopup,
	useNotistack,
	usePageChangeAfterEntityDelete
} from '../../../shared/hooks';
import { useAppState } from '../../../shared/state';
import { formRequest } from '../../../modules/entity/actions';
import { useDispatch } from 'react-redux';

export function useClientList() {
	const { companyId } = useAppState();
	const dispatch = useDispatch();
	const { showNotification } = useNotistack();

	const { forwardToPreviousPage } = usePageChangeAfterEntityDelete({
		pageKey: 'page',
		entity: 'clients',
		entityName: 'AllClients'
	});

	const {
		visible: deleteVisible,
		showVisible: showDeleteVisible,
		loading: deleteLoading,
		toggleLoading: toggleDeleteLoading,
		closePopup: closeDeletePopup
	} = useEntityActionPopup<ClientCompany>();

	const {
		visible: editVisible,
		showVisible: showEditVisible,
		closePopup: closeEditPopup
	} = useEntityActionPopup<ClientCompany>();

	const handleClientDelete = (client: ClientCompany) => {
		//show loader
		toggleDeleteLoading();
		dispatch(
			formRequest({
				entity: 'clients',
				name: `AllClients`,
				method: 'delete',
				url: `/${companyId}/client_companies/remove/${client.id}/`,
				deleteData: true,
				id: client.id,
				cb: {
					success: () => {
						showNotification({ message: 'Successfully deleted', variant: 'success' });
						forwardToPreviousPage();
					},
					error: (err) => {
						showNotification({ message: 'Something went wrong', variant: 'error' });
					},
					finally: () => {
						toggleDeleteLoading();
						closeDeletePopup();
					}
				},
				params: {},
				primaryKey: 'id',
				values: {}
			})
		);
	};

	return {
		deleteLoading,
		handleClientDelete,
		showDeleteVisible,
		closeDeletePopup,
		deleteVisible,
		editVisible,
		showEditVisible,
		closeEditPopup
	};
}
