import React, { useEffect, useState } from 'react';
import { useRouter, useUnreadCaseTab } from 'shared/hooks';

export function useClientManagement() {
	const [primeTab, setPrimeTab] = React.useState(0);
	const [pageName, setPageName] = React.useState('Clients List');

	const [inviteVisible, setInviteVisible] = useState(false);
	const [groupVisible, setGroupVisible] = useState(false);
	const [linkedRoleVisible, setLinkedRoleVisible] = useState(false);

	const { location, match } = useRouter();
	const { url, path } = match;
	const { unreadCasesQuantity, isHidden } = useUnreadCaseTab('Clients Management');

	const handleChangePrime = (
		event: React.ChangeEvent<Record<string, unknown>>,
		newValue: number
	) => {
		setPrimeTab(newValue);
	};

	const toggleInviteForm = () => setInviteVisible((prev) => !prev);
	const toggleGroupForm = () => setGroupVisible((prev) => !prev);
	const toggleLinkedRoleForm = () => setLinkedRoleVisible((prev) => !prev);

	const togglePopup = (btnText: string) => {
		switch (btnText) {
			case 'clients': {
				return toggleInviteForm;
			}
			case 'categories': {
				return toggleGroupForm;
			}
			default:
				return () => {};
		}
	};

	//Just to setup tab active tab indication
	useEffect(() => {
		const locationSplitterArray = location.pathname.split('/').filter(Boolean).filter(String);
		const nestedPageType = locationSplitterArray[locationSplitterArray.length - 1] || '';

		(() => {
			switch (nestedPageType) {
				case 'clients': {
					setPrimeTab(0);
					setPageName('Clients List');
					break;
				}
				case 'categories': {
					setPrimeTab(1);
					setPageName('Client Groups');
					break;
				}
				case 'incoming': {
					setPrimeTab(2);
					setPageName('Client Requests');
					break;
				}
				case 'outgoing': {
					setPrimeTab(2);
					setPageName('Client Requests');
					break;
				}
				case 'roles': {
					setPrimeTab(3);
					setPageName('Linked Client Roles');
					break;
				}
				default:
					return null;
			}
		})();
	}, [location]);

	return {
		primeTab,
		pageName,
		url,
		path,
		unreadCasesQuantity,
		isHidden,
		handleChangePrime,
		inviteVisible,
		toggleInviteForm,
		groupVisible,
		toggleGroupForm,
		togglePopup,
		toggleLinkedRoleForm,
		linkedRoleVisible
	};
}
