import React, { useEffect } from 'react';
import { useRouter } from '../../../shared/hooks';

export function useClientRequests() {
	const [tab, setTab] = React.useState(0);

	const { location, match } = useRouter();
	const { url, path } = match;
	// const { isStaff, setIsStaff } = useClientsManagementContext();

	const handleChangeTab = (event: React.ChangeEvent<Record<string, unknown>>, newValue: number) => {
		setTab(newValue);
	};

	//Just to setup tab active tab indication
	useEffect(() => {
		const locationSplitterArray = location.pathname.split('/');
		const nestedPageType = locationSplitterArray[locationSplitterArray.length - 1] || '';

		(() => {
			switch (nestedPageType) {
				case 'incoming': {
					setTab(0);
					break;
				}
				case 'outgoing': {
					setTab(1);
					break;
				}
				default:
					return null;
			}
		})();
	}, [location]);

	return {
		url,
		path,
		handleChangeTab,
		tab
	};
}
