import React from 'react';
import { FormHeader, Loader } from '../../../shared/components';
import { KntInputField, KntPhoneInputField } from '../../../shared/components/Fields';
import { Field } from 'formik';
import { InviteClientFormFooter } from './InviteClientFormFooter';
import { InvitationByCsv } from './InvitationByCSV';
import { IOSSwitch } from '../../../shared/components/Elements/Fields/SwitchComp';
import { FormControlLabel } from '../../../shared/components/Elements';
import { useSwitch } from '../../../shared/hooks';

type Props = {
	SwitchClient?: React.ReactNode;
	isSubmitting: boolean;
	setFieldValue: any;
	onSuccess: () => void;
	resetForm: any;
	isEditing?: boolean;
};

export function BusinessFormElements({
	SwitchClient = null,
	isSubmitting,
	setFieldValue,
	onSuccess,
	resetForm,
	isEditing = false
}: Props) {
	const { checked, handleChange } = useSwitch({ csv: false });

	return (
		<div>
			<FormHeader
				resetForm={resetForm}
				title={`${isEditing ? 'Edit' : 'Invite'} Business Client`}
				isEditing={isEditing}
			/>
			{SwitchClient ? <div>{SwitchClient}</div> : null}
			<>
				{!isEditing ? (
					<FormControlLabel
						control={<IOSSwitch checked={checked.csv} onChange={handleChange} name="csv" />}
						label="Invite via csv file uploading(optional)"
						className="mb-4"
						classes={{ label: '!text-xs font-light text-kgrey-welcome' }}
					/>
				) : null}
				{/*Invite business clients via csv */}
				{checked.csv ? (
					<InvitationByCsv onSuccess={onSuccess} />
				) : (
					<Field
						label="Business Name"
						labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
						name="name"
						component={KntInputField}
						placeholder="Type Business name here.."
						required
						autoComplete="off"
					/>
				)}
				{isEditing ? (
					<Field
						label="Phone"
						name="phone"
						component={KntPhoneInputField}
						placeholder="Enter company mobile phone number"
						enableSearch
						required
						labelClasses="!text-xs !ml-6 !text-kgrey-welcome"
						onChangeHandler={setFieldValue}
					/>
				) : null}
				<div className={'mt-8 flex items-center justify-end'}>
					{isSubmitting && <Loader tiled withOverlay />}
					<InviteClientFormFooter
						onSuccess={onSuccess}
						isSubmitting={isSubmitting}
						disableOptions={checked.csv}
						submitText={isEditing ? 'Edit Client' : 'Invite Client'}
					/>
				</div>
			</>
		</div>
	);
}
