import React, { useState } from 'react';
import { Button } from '../../../shared/components/Elements';
import Upload from 'rc-upload';
import config from '../../../config';
import { useAppState } from '../../../shared/state';
import { useAuth } from '../../../modules/auth/hooks/useAuth';
import PublishIcon from '@material-ui/icons/Publish';
import CloseIcon from '@material-ui/icons/Close';
import { makeStyles } from '@material-ui/core/styles';
import Papa from 'papaparse';
import { HtmlParser } from 'shared/components';
import isEmpty from 'lodash/isEmpty';
import { LinearProgress } from '@material-ui/core';
import { csvFileValidator, csvTools, parseNAsyncValidate } from 'shared/services';
import { useNotistack } from '../../../shared/hooks';

const useStylesProgress = makeStyles((theme) => ({
	root: {
		width: '100%',
		'& > * + *': {
			marginTop: theme.spacing(2)
		}
	}
}));

const { clientCSVConfig } = csvTools;

type Props = {
	onSuccess: () => void;
};

export function InvitationByCsv({ onSuccess }: Props) {
	const { companyId } = useAppState();
	const { token } = useAuth();

	const [loadingImage, setLoadingImage] = useState(false);
	const [fileName, setFileName] = useState(null);
	const [csvData, setCsvData] = useState([]);
	const [csvPrimaryCheckError, setCsvPrimaryCheckError] = useState<null | string>(null);

	const { showNotification } = useNotistack();

	const classesProgress = useStylesProgress();
	const CSVConfig = clientCSVConfig;
	const configHeaderLength = 7;
	const moreTxt =
		'There are more than required columns in your csv file. Please remove redundant columns. Download sample csv file and edit it accordingly.';
	const lessTxt =
		'There are missing columns in your csv file. Please add appropriate columns. Download sample csv file and edit it accordingly.';

	const removeFile = () => {
		setFileName(null);
	};

	const fileOnStart = (file: any) => {
		setLoadingImage(true);
		setFileName(file.name);
		if (file.name.slice(-3).toLowerCase() === 'csv') {
			return new Promise((resolve, reject) => {
				Papa.parse(file, {
					complete: (parsedOutput) => {
						const { data } = parsedOutput;

						//@ts-ignore
						const dataColumnLength = data[0].length;

						//Check if there required length of columns. If it passes than we validate fields.
						if (dataColumnLength === configHeaderLength) {
							// we do csv validation
							csvFileValidator(file, CSVConfig).then((csvData) => {
								if (isEmpty(csvData.inValidMessages)) {
									//Before uploading the csv we must validate email through
									// backend whether users are already client in the business
									csvData &&
										parseNAsyncValidate({
											csvData,
											companyId,
											resolve,
											reject,
											setCsvData,
											type: 'client',
											file,
											setLoadingImage
										});
								} else {
									//Show csv error messages in UI
									setCsvData(
										csvData.inValidMessages
											.map((message: any) =>
												message.startsWith('<div')
													? message
													: `<div className="red">${message}</div>`
											)
											.join('')
									);
									reject(new Error('Invalid csv file'));
									setLoadingImage(false);
									removeFile();
								}
							});
						} else {
							if (dataColumnLength > configHeaderLength) {
								setCsvPrimaryCheckError(moreTxt);
								setCsvData([]);
							} else if (dataColumnLength < configHeaderLength) {
								setCsvPrimaryCheckError(lessTxt);
								setCsvData([]);
							}
							reject(new Error('Invalid csv file'));
							removeFile();
							setLoadingImage(false);
						}
					}
				});
			});
		} else {
			setLoadingImage(false);
			showNotification({
				message: 'Invalid file type. Download please sample of file csv',
				variant: 'error'
			});
			return false;
		}
	};

	const fileOnSuccess = (file: any) => {
		setLoadingImage(false);
		showNotification({ message: 'Successfully uploaded and invited', variant: 'success' });
		//Close popup
		onSuccess();
	};

	return (
		<div className="mb-4">
			<div className="mb-4 flex items-center justify-between">
				<a
					href="https://kennekt.com/files/kennekt_client.csv"
					download
					className="text-sm text-primary transition hover:text-primary-dark"
				>
					Download CSV sample file
				</a>
				<div className="uploader">
					<Upload
						accept=".csv"
						className="upload-file-styles"
						action={`${config.API_ROOT}/${companyId}/import-clients/`}
						//@ts-ignore
						beforeUpload={fileOnStart}
						onSuccess={fileOnSuccess}
						headers={{
							Authorization: 'Token ' + token
						}}
					>
						<Button variant="outlined" aria-label="file-attach" className="mr-5">
							<PublishIcon /> Upload CSV file
						</Button>
					</Upload>
				</div>
			</div>
			<div className="file-name">
				{fileName !== null ? (
					<div className="flex items-center justify-between">
						<p className="">{fileName}</p>
						<CloseIcon
							onClick={removeFile}
							className="cursor-pointer transition hover:text-primary"
						/>
					</div>
				) : null}
			</div>
			<div className="linear-loader mt-2">
				{loadingImage ? (
					<div className={classesProgress.root}>
						<LinearProgress className="loading-file" />
					</div>
				) : null}
			</div>
			<div className="csv-error-display mt-2">
				{!isEmpty(csvData) && (
					<div className="my-5">
						<div>
							{/*@ts-ignore*/}
							<HtmlParser text={csvData} />
							<div className="text-sm text-kred-pink">
								<strong>Please correct and upload again</strong>
							</div>
						</div>
					</div>
				)}
			</div>
			<div className="primary-error-check">
				{csvPrimaryCheckError && (
					<div className="mb-5">
						<div className="mt-5 text-sm text-kred-pink">
							<strong>{csvPrimaryCheckError}</strong>
						</div>
					</div>
				)}
			</div>
		</div>
	);
}
