import React from 'react';
import AsyncSelectJs from 'shared/components/Fields/AsyncSelectJs';
import { KntTwilioStyle } from 'shared/services';
import { Field } from 'formik';
import { useAppState } from 'shared/state';
import { KntInputField } from 'shared/components/Fields';
import { InviteClientFormFooter } from '../InviteClientFormFooter';

type Props = {
	isSubmitting: boolean;
	onSuccess: () => void;
	isEditing?: boolean;
};
function ClientGroupElements({ isSubmitting, onSuccess, isEditing = false }: Props) {
	const { companyId } = useAppState();

	return (
		<div className="w-full">
			<Field
				label="Client Group Name:"
				labelClasses="ml-6 text-xs font-light text-kgrey-welcome mb-2 block"
				name="name"
				component={KntInputField}
				placeholder="Type your firstname.."
				required
				autoComplete="off"
			/>
			<>
				<label className="ml-6 mb-2 block text-xs font-light text-kgrey-welcome">
					<span>Search Client:</span>
				</label>
				<div className="react-select-dropdown">
					<Field
						component={AsyncSelectJs}
						customStylesProps={KntTwilioStyle}
						name="clients"
						placeholder="Select Clients Here"
						optionLabel="name"
						optionValue="id"
						isSearchable
						isMulti
						isClearable
						loadOptionsKey="results"
						loadOptionsUrl={`/${companyId}/client_companies/`}
						loadOptionsParams={(search: string) => ({
							extra: {
								search
							}
						})}
					/>
				</div>
			</>
			<div className="flex-center-between mt-8">
				<InviteClientFormFooter
					onSuccess={onSuccess}
					isSubmitting={isSubmitting}
					submitText={`${!isEditing ? 'Create' : 'Edit'} Client Group`}
				/>
			</div>
		</div>
	);
}

export default ClientGroupElements;
