import React from 'react';
import { BusinessFormElements } from './BusinessFormElements';
import EntityForm from '../../../modules/entity/forms';
import { useAppState } from '../../../shared/state';
import { useNotistack, useWhiteModal } from '../../../shared/hooks';
import { InviteCreateNewBusinessForm } from './InviteCreateNewBusinessForm';

type Props = {
	SwitchClient: React.ReactNode;
	onSuccess: () => void;
};

export function BusinessInviteForm({ SwitchClient, onSuccess }: Props) {
	const { companyId } = useAppState();
	const { showNotification } = useNotistack();
	const { WhiteModal, modalHandler, open } = useWhiteModal();

	return (
		<EntityForm.Main
			entity="clients"
			name="AllClients"
			url={`/${companyId}/clients/`}
			method="post"
			primaryKey="id"
			sendAsFormData={false}
			// normalizeData={(data: any) => data}
			// prependData
			fields={[
				{
					name: 'id',
					required: false,
					value: null,
					onSubmitKey: ''
				},
				{
					name: 'is_individual',
					required: true,
					value: false,
					onSubmitKey: ''
				},
				{
					name: 'name',
					onSubmitKey: '',
					required: true,
					value: ''
				},
				{
					name: 'first_name',
					onSubmitKey: '',
					value: null
				},
				{
					name: 'last_name',
					onSubmitKey: '',
					value: null
				},
				{
					name: 'mobile',
					onSubmitKey: '',
					required: false,
					value: null
				},
				{
					name: 'email',
					onSubmitKey: '',
					value: null
				}
			]}
			onSuccess={(response, resetForm): any => {
				showNotification({ message: 'Success! Waiting for client accepting..' });
				onSuccess();
				resetForm();
			}}
			onError={(error, resetForm): any => {
				if (error?.response?.data?.message === 'Company does not exists\n') {
					showNotification({ message: 'Client does not exist', variant: 'error' });
					modalHandler();
				}
				if (error?.response?.data?.message !== 'Company does not exists\n') {
					showNotification({ message: error?.response?.data?.message, variant: 'error' });
				}
			}}
		>
			{({ isSubmitting, values, setFieldValue, resetForm }) => {
				if (process.env.NODE_ENV === 'development') {
					console.log('business invite create values', values);
				}
				return (
					<>
						<BusinessFormElements
							setFieldValue={setFieldValue}
							isSubmitting={isSubmitting}
							SwitchClient={SwitchClient}
							onSuccess={onSuccess}
							resetForm={resetForm}
						/>
						{/*Create New Client if Business Client does not exist*/}
						{/*<WhiteModal handleModalOpen={modalHandler} open={open} maxWidth={446}>
							<InviteCreateNewBusinessForm
								businessName={values.name}
								onSuccess={modalHandler}
								parentOnSuccess={onSuccess}
							/>
						</WhiteModal>*/}
					</>
				);
			}}
		</EntityForm.Main>
	);
}
