import {
	useEntityActionPopup,
	useLoadAll,
	useNotistack,
	usePageChangeAfterEntityDelete
} from 'shared/hooks';
import { OutgoingRequestType } from 'shared/typings/requests.type';
import { useDispatch } from 'react-redux';
import { formRequest } from '../../../modules/entity/actions';
import { useAppState } from '../../../shared/state';
import get from 'lodash/get';

export function useOutgoingRequests() {
	const dispatch = useDispatch();
	const { companyId } = useAppState();
	const { reloadEntityAll } = useLoadAll();

	const {
		visible: deleteVisible,
		showVisible: showDeleteVisible,
		loading: deleteLoading,
		toggleLoading: toggleDeleteLoading,
		closePopup: closeDeletePopup
	} = useEntityActionPopup<OutgoingRequestType>();

	const {
		visible: resendVisible,
		showVisible: showResendVisible,
		loading: resendLoading,
		toggleLoading: toggleResendLoading,
		closePopup: closeResendPopup
	} = useEntityActionPopup<OutgoingRequestType>();

	const { showNotification } = useNotistack();

	const { forwardToPreviousPage } = usePageChangeAfterEntityDelete({
		pageKey: 'page',
		entity: 'outgoing-request',
		entityName: 'AllOutgoing-Requests'
	});

	const handleDeleteRequest = (request: OutgoingRequestType) => {
		toggleDeleteLoading();
		dispatch(
			formRequest({
				entity: 'outgoing-request',
				name: 'AllOutgoing-Requests',
				method: 'delete',
				url: `/${companyId}/my_company_connection_requests/${request.id}/`,
				deleteData: true,
				id: request.id,
				cb: {
					success: () => {
						showNotification({ message: 'Successfully deleted', variant: 'success' });
						forwardToPreviousPage();
						closeDeletePopup();
					},
					error: (err) => {
						showNotification({ message: 'Something went wrong', variant: 'error' });
					},
					finally: () => {
						toggleDeleteLoading();
					}
				},
				params: {},
				primaryKey: 'id',
				values: {}
			})
		);
	};

	const handleResendRequest = (request: OutgoingRequestType) => {
		toggleResendLoading();
		dispatch(
			formRequest({
				entity: 'outgoing',
				name: 'AllOutgoing',
				method: 'patch',
				url: `/${companyId}/my/company/clients/${get(
					request,
					'requested_company.id'
				)}/resend_invitation/`,
				values: {},
				cb: {
					success: () => {
						//Refetch Incoming Requests
						reloadEntityAll({
							entity: 'outgoing-request',
							name: 'AllOutgoing-Requests',
							url: `/${companyId}/my_company_connection_requests/`,
							params: {
								page: 1,
								limit: 10
							}
						});

						closeResendPopup();

						showNotification({ message: 'Successfully resended', variant: 'success' });
					},
					error: (err) => {
						showNotification({ message: 'Something went wrong', variant: 'error' });
					},
					finally: () => {
						toggleResendLoading();
					}
				},
				params: {},
				primaryKey: 'id'
			})
		);
	};

	return {
		deleteVisible,
		showDeleteVisible,
		deleteLoading,
		toggleDeleteLoading,
		closeDeletePopup,
		resendVisible,
		showResendVisible,
		resendLoading,
		toggleResendLoading,
		closeResendPopup,
		handleDeleteRequest,
		handleResendRequest
	};
}
