import React from 'react';
import { ClientType } from './InviteClientForm';

type Props = {
	clientType: ClientType;
	setClientType: any;
};

export function ClientInviteFormSwitch({ clientType, setClientType }: Props) {
	return (
		<>
			<div className="switch-toggle-custom switch-ios mb-4 font-poppins">
				<input
					id="client"
					name="casetype"
					type="radio"
					defaultChecked={clientType === 'Individual'}
					tabIndex={-1}
				/>
				<label htmlFor="client" onClick={() => setClientType('Individual')}>
					Individual
				</label>
				<input
					id="Staff"
					name="casetype"
					type="radio"
					defaultChecked={clientType === 'Business'}
					tabIndex={-1}
				/>
				<label htmlFor="Staff" onClick={() => setClientType('Business')}>
					Business
				</label>
				<a />
			</div>
		</>
	);
}
