import {
	useEntityActionPopup,
	useLoadAll,
	useNotistack,
	usePageChangeAfterEntityDelete
} from 'shared/hooks';
import { IncomingRequestType } from 'shared/typings/requests.type';
import { useDispatch } from 'react-redux';
import { formRequest } from '../../../modules/entity/actions';
import { useAppState } from '../../../shared/state';

export function useIncomingRequests() {
	const dispatch = useDispatch();
	const { companyId } = useAppState();
	const { reloadEntityAll } = useLoadAll();

	const {
		visible: rejectVisible,
		showVisible: showRejectVisible,
		loading: rejectLoading,
		toggleLoading: toggleRejectLoading,
		closePopup: closeRejectPopup
	} = useEntityActionPopup<IncomingRequestType>();

	const {
		visible: editVisible,
		showVisible: showEditVisible,
		loading: editLoading,
		toggleLoading: toggleEditLoading,
		closePopup: closeEditPopup
	} = useEntityActionPopup<IncomingRequestType>();

	const { showNotification } = useNotistack();

	const { forwardToPreviousPage } = usePageChangeAfterEntityDelete({
		pageKey: 'page',
		entity: 'incoming-request',
		entityName: 'AllIncoming-Requests'
	});

	const handleStatusChange = (id: number, type: string) => {
		const isReject = type === 'reject';
		isReject ? toggleRejectLoading() : toggleEditLoading();
		dispatch(
			formRequest({
				entity: 'linked-clients',
				name: 'AllLinkedClients',
				method: 'post',
				url: `/${companyId}/company_connection_requests/${id}/${type}/`,
				id,
				values: {},
				cb: {
					success: () => {
						const notifText = isReject ? 'Successfully Rejected' : 'Successfully Accepted';

						//Refetch Incoming Requests
						reloadEntityAll({
							entity: 'incoming-request',
							name: 'AllIncoming-Requests',
							url: `/${companyId}/company_connection_requests/`,
							params: {
								page: 1,
								limit: 10
							}
						});

						closeRejectPopup();
						closeEditPopup();
						forwardToPreviousPage();
						showNotification({ message: notifText, variant: 'success' });
					},
					error: (err) => {
						showNotification({ message: 'Something went wrong', variant: 'error' });
					},
					finally: () => {
						type === 'reject' ? toggleRejectLoading() : toggleEditLoading();
					}
				},
				params: {},
				primaryKey: 'id'
			})
		);
	};

	return {
		rejectVisible,
		showRejectVisible,
		rejectLoading,
		toggleRejectLoading,
		closeRejectPopup,
		editVisible,
		showEditVisible,
		editLoading,
		toggleEditLoading,
		closeEditPopup,
		handleStatusChange
	};
}
