import React, { lazy, Suspense, useCallback } from 'react';
import { ConfirmModal, CustomMuiTabs, ErrorBoundary, Loader } from 'shared/components';
import { Helmet } from 'react-helmet';
import { ClientsSearchQuery } from './components/ClientsSearchQuery';
import { Link, Route, Switch } from 'react-router-dom';
import { ClientsManagementContextProvider } from './context/ClientsManagementContext';
import { useClientManagement } from './hooks';
import { SimpleBtn, WhiteModal } from 'shared/components/Elements';
import { InviteClientForm } from './components';
import { StandardPageLayout } from 'shared/layouts';
import { CreateClientGroup } from './components/ClientGroups';
import { CreateLinkedClientRole } from './components/LinkenClientRoles';
import { useAppState } from '../../shared/state';
import { useFormGuard, useRouter } from '../../shared/hooks';

const ClientsList = lazy(() => import('./NestedPages/ClientsList'));
const ClientsGroups = lazy(() => import('./NestedPages/ClientsGroups'));
const ClientsRequests = lazy(() => import('./NestedPages/ClientsRequests'));
const LinkedClientsRoles = lazy(() => import('./NestedPages/LinkedClientsRoles'));
const ClientDetails = lazy(() => import('./NestedPages/ClientDetails'));
const ClientGroupDetails = lazy(() => import('./NestedPages/ClientGroupDetails'));

function a11yProps(index: number) {
	return {
		id: `simple-tabpanel-${index}`,
		'aria-controls': `simple-tab-${index}`
	};
}

function ClientsManagementInner() {
	const {
		primeTab,
		pageName,
		url,
		path,
		unreadCasesQuantity,
		isHidden,
		handleChangePrime,
		inviteVisible,
		toggleInviteForm,
		groupVisible,
		toggleGroupForm,
		toggleLinkedRoleForm,
		linkedRoleVisible
	} = useClientManagement();

	const { isPermitted } = useAppState();

	const { formValues, guardOpen, setGuardOpen, formValuesSetter } = useFormGuard();
	const { location } = useRouter();

	const hasToShowSearchQuery = !location.pathname.includes('request');

	const confirmToggleHandler = useCallback(() => {
		setGuardOpen((prev: boolean) => !prev);
	}, []);
	const confirmModalHandler = useCallback(() => {
		setGuardOpen(false);
		formValuesSetter([]);
		toggleInviteForm();
	}, [inviteVisible]);

	const hasToShowGuard = useCallback(() => {
		if (formValues.length) {
			setGuardOpen(true);
			return;
		}

		toggleInviteForm();
	}, [formValues]);

	const onPopupClose = () => {
		if (formValues.length) {
			setGuardOpen(true);
			return;
		}
		toggleInviteForm();
	};

	return (
		<StandardPageLayout>
			<div className="flex min-h-full max-w-full flex-grow flex-col lg:p-4">
				{(!isHidden || unreadCasesQuantity === 0) && (
					<Helmet>
						<title>{`KENNEKT | ${pageName}`}</title>
					</Helmet>
				)}
				<div className="mb-5 flex items-center justify-between">
					<div className="flex w-full flex-wrap items-center justify-between">
						<h2 className="py-4 text-xl font-bold dark:text-white md:text-3xl">
							Clients Management
						</h2>
						<div className="flex w-full items-center md:w-auto">
							{hasToShowSearchQuery && <ClientsSearchQuery />}
							{primeTab === 0 ? (
								<SimpleBtn disabled={!isPermitted} onClick={toggleInviteForm}>
									New Client
								</SimpleBtn>
							) : null}
							{primeTab === 1 ? (
								<SimpleBtn disabled={!isPermitted} onClick={toggleGroupForm}>
									New Client Group
								</SimpleBtn>
							) : null}
							{primeTab === 3 ? (
								<SimpleBtn disabled={!isPermitted} onClick={toggleLinkedRoleForm}>
									New Linked Client Role
								</SimpleBtn>
							) : null}
							<WhiteModal handleModalOpen={onPopupClose} open={inviteVisible} maxWidth={446}>
								<InviteClientForm
									hasToShowGuard={hasToShowGuard}
									formValuesSetter={formValuesSetter}
									modalHandler={toggleInviteForm}
								/>
							</WhiteModal>
							<WhiteModal handleModalOpen={toggleGroupForm} open={groupVisible} maxWidth={446}>
								<CreateClientGroup onSuccess={toggleGroupForm} />
							</WhiteModal>
							<WhiteModal
								handleModalOpen={toggleLinkedRoleForm}
								open={linkedRoleVisible}
								maxWidth={446}
							>
								<CreateLinkedClientRole onSuccess={toggleLinkedRoleForm} />
							</WhiteModal>
							<ConfirmModal
								featureHandler={confirmModalHandler}
								isOpen={guardOpen}
								toggleHandler={confirmToggleHandler}
								title="You have unsaved changes, Are you sure to cancel?"
							/>
						</div>
					</div>
				</div>
				<div className="prime-tabs-clients overflow-x-auto">
					<div className="prime-tabs">
						<CustomMuiTabs.Tabs
							roundedTop
							value={primeTab}
							// @ts-ignore
							onChange={handleChangePrime}
							aria-label="simple tabs"
						>
							<CustomMuiTabs.Tab
								label="All Clients"
								component={Link}
								to={`${url}/clients`}
								{...a11yProps(0)}
							/>
							<CustomMuiTabs.Tab
								label="Clients Group"
								component={Link}
								to={`${url}/categories`}
								{...a11yProps(1)}
							/>
							<CustomMuiTabs.Tab
								label="Requests"
								component={Link}
								to={`${url}/requests/incoming`}
								{...a11yProps(2)}
							/>
							<CustomMuiTabs.Tab
								label="Linked Client Roles"
								component={Link}
								to={`${url}/roles`}
								{...a11yProps(3)}
							/>
						</CustomMuiTabs.Tabs>
					</div>
				</div>

				<div className="relative flex max-w-full flex-grow flex-col">
					<div className="relative flex-grow">
						<Suspense fallback={<Loader tiled />}>
							<Switch>
								<Route exact path={`${path}/clients/`} component={ClientsList} />
								<Route exact path={`${path}/categories/`} component={ClientsGroups} />
								<Route exact path={`${path}/requests/incoming/`} component={ClientsRequests} />
								<Route exact path={`${path}/requests/outgoing/`} component={ClientsRequests} />
								<Route exact path={`${path}/roles/`} component={LinkedClientsRoles} />
								<Route
									exact
									path={`${path}/categories/:clientGroupId`}
									component={ClientGroupDetails}
								/>
								<Route exact path={`${path}/clients/:clientId`} component={ClientDetails} />
							</Switch>
						</Suspense>
					</div>
				</div>
			</div>
		</StandardPageLayout>
	);
}

export function ClientsManagement() {
	return (
		<ErrorBoundary>
			<ClientsManagementContextProvider>
				<ClientsManagementInner />
			</ClientsManagementContextProvider>
		</ErrorBoundary>
	);
}
