import React from 'react';
import { IndividualFormElements } from './IndividualFormElements';
import EntityForm from '../../../modules/entity/forms';
import { useAppState } from '../../../shared/state';
import { useNotistack } from '../../../shared/hooks';

type Props = {
	SwitchClient: React.ReactNode;
	onSuccess: () => void;
	initialIndividualClientCompany: {
		id: number;
	};

	formValuesSetter: (fieldValues: string[]) => void;
	hasToShowGuard: () => void;
};

export function IndividualInviteForm({
	SwitchClient,
	onSuccess,
	initialIndividualClientCompany,
	formValuesSetter,
	hasToShowGuard
}: Props) {
	const { companyId } = useAppState();
	const { showNotification } = useNotistack();

	return (
		<>
			<EntityForm.Main
				entity="clients"
				name="AllClients"
				url={`/${companyId}/clients/`}
				method="post"
				primaryKey="id"
				sendAsFormData={false}
				// normalizeData={(data: any) => data}
				// prependData
				fields={[
					{
						name: 'first_name',
						required: true,
						value: '',
						onSubmitKey: ''
					},
					{
						name: 'last_name',
						required: true,
						value: '',
						onSubmitKey: ''
					},
					{
						name: 'email',
						onSubmitKey: '',
						required: true,
						value: ''
					},
					{
						name: 'mobile',
						onSubmitKey: '',
						required: true,
						value: ''
					},
					{
						name: 'is_individual',
						onSubmitKey: '',
						value: true
					},
					{
						name: 'name',
						value: null,
						onSubmitKey: ''
					}
				]}
				onSuccess={(response, resetForm): any => {
					showNotification({ message: 'Success! Waiting for client accepting..' });
					onSuccess();
					resetForm();
				}}
				onError={(error, resetForm): any => {
					console.log(error, 'Individual client form');

					const errorData = error && error.response;
					let message = errorData?.data?.message || 'Something went wrong';

					if (errorData.status === 400 && message.match('Already connected')) {
						message = 'Client Already Has Been Connected';
					}

					showNotification({ message, variant: 'error' });
				}}
			>
				{({ isSubmitting, values, setFieldValue, resetForm }) => {
					if (process.env.NODE_ENV === 'development') {
						console.log('individual invite create values', values);
					}
					return (
						<IndividualFormElements
							setFieldValue={setFieldValue}
							values={values}
							isSubmitting={isSubmitting}
							SwitchClient={SwitchClient}
							onSuccess={hasToShowGuard}
							resetForm={resetForm}
							formValuesSetter={formValuesSetter}
						/>
					);
				}}
			</EntityForm.Main>
		</>
	);
}
