import { LinkedClientEntity, LinkedClientRoleInList } from 'shared/typings/client.types';
import { formRequest } from 'modules/entity/actions';
import { useDispatch } from 'react-redux';
import { useEntityActionPopup, useNotistack, usePageChangeAfterEntityDelete } from 'shared/hooks';
import { useAppState } from 'shared/state';

export function useLinkedClient() {
	const dispatch = useDispatch();
	const { showNotification } = useNotistack();
	const { companyId } = useAppState();

	const {
		visible: deleteVisible,
		showVisible: showDeleteVisible,
		loading: deleteLoading,
		toggleLoading: toggleDeleteLoading,
		closePopup: closeDeletePopup
	} = useEntityActionPopup<LinkedClientEntity>();

	const { forwardToPreviousPage } = usePageChangeAfterEntityDelete({
		pageKey: 'page',
		entity: 'linked-clients',
		entityName: 'AllLinkedClients'
	});

	const handleLinkedClientDelete = (linkedClient: LinkedClientEntity) => {
		toggleDeleteLoading();

		dispatch(
			formRequest({
				entity: 'linked-clients',
				name: 'AllLinkedClients',
				method: 'delete',
				url: `/${companyId}/my/company/linked_client/${linkedClient.id}/`,
				deleteData: true,
				id: linkedClient.id,
				values: {},
				cb: {
					success: () => {
						forwardToPreviousPage();
						showNotification({ message: 'Successfully deleted!', variant: 'success' });
					},
					error: (err) => {
						showNotification({ message: 'Something went wrong', variant: 'error' });
					},
					finally: () => {
						toggleDeleteLoading();
						closeDeletePopup();
					}
				},
				params: {},
				primaryKey: 'id'
			})
		);
	};

	const handleRoleUpdate = (role: LinkedClientRoleInList, client: LinkedClientEntity) => {
		dispatch(
			formRequest({
				entity: 'linked-clients',
				name: 'AllLinkedClients',
				method: 'patch',
				url: `/${companyId}/my/company/linked_client/${client.id}/`,
				updateData: true,
				id: client.id,
				values: { role: role.id },
				cb: {
					success: () => {
						showNotification({ message: 'Role is successfully changed!', variant: 'success' });
					},
					error: (err) => {
						showNotification({ message: 'Something went wrong', variant: 'error' });
					},
					finally: () => {}
				},
				params: {},
				primaryKey: 'id',
				normalizeData: (data) => data
			})
		);
	};

	return {
		handleRoleUpdate,
		deleteVisible,
		showDeleteVisible,
		deleteLoading,
		toggleDeleteLoading,
		closeDeletePopup,
		handleLinkedClientDelete
	};
}
