import React, { useState } from 'react';
import { ClientInviteFormSwitch } from './ClientInviteFormSwitch';
import { IndividualInviteForm } from './IndividualInviteForm';
import { BusinessInviteForm } from './BusinessInviteForm';
import { useAxios } from '../../../shared/hooks';
import { useAppState } from '../../../shared/state';
import { Loader } from '../../../shared/components';

type Props = {
	modalHandler: () => void;
	hasToShowGuard: () => void;
	formValuesSetter: (fieldValues: string[]) => void;
};

export type ClientType = 'Individual' | 'Business';

export type IndividualNotActiveCompany = {
	id: number;
};

export function InviteClientForm({ modalHandler, hasToShowGuard, formValuesSetter }: Props) {
	const [clientType, setClientType] = useState<ClientType>('Individual');
	const { companyId } = useAppState();

	const { response: initialIndividualClientCompany, loading } = useAxios({
		url: `/${companyId}/my/company/clients/`,
		method: 'post',
		values: {
			is_individual: true,
			status: 'Not Active'
		},
		initialLoad: true
	});

	return (
		<div>
			{clientType === 'Individual' && initialIndividualClientCompany ? (
				<IndividualInviteForm
					SwitchClient={
						<ClientInviteFormSwitch clientType={clientType} setClientType={setClientType} />
					}
					onSuccess={modalHandler}
					hasToShowGuard={modalHandler}
					initialIndividualClientCompany={
						initialIndividualClientCompany as IndividualNotActiveCompany
					}
					formValuesSetter={formValuesSetter}
				/>
			) : (
				<BusinessInviteForm
					SwitchClient={
						<ClientInviteFormSwitch clientType={clientType} setClientType={setClientType} />
					}
					onSuccess={modalHandler}
				/>
			)}
			{loading ? <Loader tiled /> : null}
		</div>
	);
}
